.Product{
    max-width: 806px;
    height: 109px;
    display: flex;
    background-color: #ffffff;

    margin: 0 40px 0 30px;

}
.ProductImg{
    padding: 8px 18px 0 16px;
     width: 100px;
     height: 75px;
}
.ProductImg img{
    width: 100px;
    height: 75px;
}
.ProductName{
    width: 358px;
    display: flex;
    flex-direction: column;

}
.ProductNameItem{
    padding: 3px 0 0 0;
    flex: 1 1 auto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #00145C;
}
.ProductNameLoca{
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    padding-bottom: 22px;
}

.ProductInput input{
    margin: 7px 0 0 0;
    width: 22px;
    height: 22px;
    border: 1px solid #ADB6D8;
    box-sizing: border-box;
    border-radius: 3px;
}


.ProductCount{
    margin: 0 0 0 46px;
    width: 100px;

}
.ProductMaxCount{
    margin: 4px 0 0 8px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #6C6C6C;
    opacity: 0.6;
}
.ProductButton{
    width: 27px;
    height: 27px;
    background: #EBF0F3;
    border-radius: 50%;
    border: 0;
    font-size: 20px;



}
.ProductButtonCount{
    padding: 0 10px 0 8px;

}
.ProductPrice{
    text-align: right;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    width: 104px;

}
.ProductCountPrice{
    display: flex;

}
.ProductRight{


}
.ProductArti{
    width: 109px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #6C6C6C;
    opacity: 0.6;
    margin: 24px 0 0 162px;


}
.ProductDelete{
    margin: 5px 0 0 0;
}
.ProductDeleteClose{
    position: absolute;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}
.ProductDeleteClose:hover {
    opacity: 1;
}
.ProductDeleteClose:before, .ProductDeleteClose:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 15px;
    width: 2px;
    background-color: #333;
}
.ProductDeleteClose:before {
    transform: rotate(45deg);
}
.ProductDeleteClose:after {
    transform: rotate(-45deg);
}