@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');
.BasketWrapper{

    margin: 0 auto;
    width: 1406px;
    min-height: 100vh;
    background: #EFF5F8;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
}
.BasketWrapperInput input{
    margin: 7px 0 0 0;
    width: 22px;
    height: 22px;
    border: 1px solid #ADB6D8;
    box-sizing: border-box;
    border-radius: 3px;
}
.BasketWrapperPath{
    font-size: 12px;
    line-height: 16px;
    color: #768790;
    width: 200px;
    height: 20px;
    margin: 16px 0 0 84px;


}
.BasketWrapperName{
    font-size: 30px;
    line-height: 41px;
    margin: 2px 0 0 83px;

}
.BasketWrapperInput{
    font-size: 14px;
    line-height: 19px;
    color: #768790;
    margin: 11px 0 0 85px;
    display: flex;

}
.BasketWrapperInputItem{
    padding: 7px 0 0 11px;

}
.BasketСontent{
    margin: 19px auto 0 auto;
    width: 1236px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
