
.FullPrice{
    width: 322px;
    height: 511px;
    background: #FFFFFF;
    border: 1px solid #CED2E1;
    border-radius: 6px;
    margin-top: 26px;
}
.FullPriceWrapper{
    width: 263px;
    margin: 0 auto;
    font-size: 14px;
    line-height: 19px;
    color: #848DAF;
}
.FullPriceCount{
    border-bottom: 1px solid rgba(173, 182, 216, 0.3);
    padding: 21px 0 21px 0;
}
.FullPriceTitle{
    font-size: 20px;
    line-height: 27px;
    color: #000000;
}
.FullPriceTotalCount{
    padding: 21px 0 0 0;
    display: flex;
    justify-content: space-between;
}
.FullPriceFlex{
    display: flex;
    justify-content: space-between;
    padding: 0px 0 5px 0;
}
.FullPriceDeliveryTitle{
    padding: 17px 0 14px 0;
}
.FullPriceDeliveryData{
    border-bottom: 1px solid rgba(173, 182, 216, 0.3);
}
.FullPriceSum{
    padding: 8px 0 16px 0;
}
.FullPriceItem{
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
    color: #000000;
}
.FullPriceDiscountItem{
    color: #ED3135;
    font-weight: bold;
}
.FullPriceDiscount{
    border-bottom: 1px solid rgba(173, 182, 216, 0.3);
    padding: 18px 0 16px 0;
}
.FullPriceTotal{
    font-size: 20px;
    line-height: 27px;
    padding: 15px 0 0 0;
}
.FullPriceTotalItem{
    color: #000000;
}
.FullPriceTotalMein{
    font-weight: bold;
    color: #5CBE00;
}
.FullPriceButton {
    border: none;
    background: #243DA1;
    border-radius: 3px;
    color: white;
    width: 253px;
    height: 40px;
    margin: 19px 0 0 0;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
}
.PromoСode{
    font-size: 14px;
    line-height: 19px;
    color: #848DAF;
    margin: 12px 0 0 0;

}
.PromoСodeItem{
    border-bottom: 1px dashed #848DAF;
}

