    line-height: 19px;
    color: #848DAF;
}

.DeliveryRadio {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.DeliveryRadio+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
}

.DeliveryRadio+label::before {
    content: '';
    display: inline-block;
    width: 23px;
    height: 23px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #ADB6D8;
    border-radius: 50%;
    margin-right: 9px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.DeliveryRadio:not(:disabled):not(:checked)+label:hover::before {
    border-color: #b3d7ff;
}

.DeliveryRadio:not(:disabled):active+label::before {
    background-color: #b3d7ff;
    border-color: #b3d7ff;
}

.DeliveryRadio:focus:not(:checked)+label::before {
    border-color: #80bdff;
}

.DeliveryRadio:checked+label::before {
    color: #6739ff;
    background-color: #243DA1;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
}
.DeliveryRadio:checked+label {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
    color: #000000;
}
